@media (min-width:280px){
    .container{
        display: block;
        justify-content: space-between;
        margin-top: 8%;
    }
    .centra{
        width: 100%;
        background-color:var(--primeira-color);
        padding: 0rem;
    }
    .ong{
        display: flex;
        gap: 3%;
        margin-top: 1rem;
        justify-content: center;
    }
    .ico{
        display: flex;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.356);
        padding: 1rem 2px;
        margin: auto;
    }
    .texCentral{
        width: 100%;
    }
    .container .texCentral img{
        width: 150%;
        margin-left: -30%;
        display: none;
    }
    .container .texCentral h1{
       font-size: 1.3rem;
       font-family: cursive;
       padding-bottom: 0.5rem;
    }
    .espaço{
        display: flex;
        gap: 5%;
        justify-content: center;
    }
    .texCentral span{
       font-family: var(--font-principal);
       color: var(--quarta-color);
       font-size: 3rem;
       background-color: var(--primeira-color);
       padding: .5rem;
       border-radius: 5px;
       box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
       cursor: pointer;
    }
    .texCentral span:hover{
       background-color: var(--quarta-color);
       color: var(--primeira-color);
       padding: .8rem;
       border-radius: 5px;
       box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
    }
    .centra label{
        font-family: Arial, Helvetica, sans-serif;
       color: var(--quarta-color);
       font-size: .9rem;
       font-weight: 600;
       text-align: center;
    }
    .container .texCentral h4{
        margin-top: 0px;
        font-size: 2.5rem;
        color: var(--primeira-color);
    }
    .cont .form form{
        width: 100%;
    }
    .form h3{
        color: aliceblue;
        font-size: 1.7rem;
    }
    .cont .form form span{
        font-size: .9rem;
        margin: 5px;
        padding: .5rem;
    }
    .form label input{
        width: 90%;
        height: 25px;
        border-radius: 5px;
        text-align: center;
        border: .2px solid rgba(0, 0, 0, 0.281);
    }
    .form label textarea{
        width: 90%;
        height: 60px;
        margin: auto;
    } 
    .form button {
        color: #14234b;
        background-color: #ffffff;
        font-size: 1rem;
        font-weight: 600;
        border: none;
        border-radius: 3px;
        padding: 0.3rem 3rem;
        box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.415);
        cursor: pointer;
        margin: 1rem;
    }
    .form button:hover {
        color: #ffffff;
        background-color:#14234b ;
        transform: scale(1.3);
    }
}
@media (min-width:375px){
    .container .texCentral h4{
        margin-top: 12%;
    }
}
@media (min-width:540px){
    .container .texCentral h4{
        margin-top: -2%;
    }
    .container .texCentral h1{
        font-size: 1.5rem;
     }
     .texCentral span{
        font-size: 4rem;
        padding: 1rem;

     }
     .texCentral span:hover{
        padding: .8rem;
     }
     .centra label{
        font-size: 1rem;
        font-weight: 600;
     }
     .container .texCentral h4{
         margin-top: 0px;
         font-size: 2.9rem;
     }
     .form h3{
        font-size: 2.2rem;
    }
    .cont .form form span{
        font-size: 1rem;
        margin: 5px;
        padding: .5rem;
    }
    .form label input{
        width: 90%;
        height: 30px;
    }
    .form label textarea{
        width: 90%;
        height: 60px;
    } 
    .form button {
        font-size: 1.1rem;
        padding: 0.5rem 3rem;
        margin: 1rem;
    }
}
@media (min-height: 280px) and (orientation: landscape) {
    .container{
        display: flex;
        justify-content: space-between;
        margin-top: 0%;
    }
    .container .texCentral img{
        width: 150%;
        margin-left: -30%;
        display: block;
    }
  
    .container .texCentral h4{
        margin-bottom: 1rem;
        margin-top: 2rem;
        font-size: 2.5rem;
    }
    .form h3{
        font-size: 2.2rem;
        margin-top: 13%;
    }
    .centra label{
        font-size: .9rem;
     }
    
    .container .texCentral h1{
        font-size: 1.9rem;
     }
     .texCentral span{
        font-size: 2.4rem;
     }
     .texCentral span:hover{
        font-size: 2.4rem;
     }
     .form label input{
        height: 30px;
    }
    .form label textarea{
        height: 120px;
    } 
}
