
@media (min-width:280px){
    .responsivo{
        display:none;
        cursor: pointer;
    }
    .responsivo:hover ul{
        display:block;
    }
    .responsivo ul{
        padding: 4px;
        display: none;
        position: absolute;
        background-color: rgba(0, 0, 0, 0.404);
        border-radius: 0px 0px 5px 0px;
        text-align: center;
        transition: 1s;
        margin-top: -5px;
    }
    .div{
        background-color:transparent;
        position: fixed;
        top: 0;
        z-index: 1000;
        
    }
    nav{ 
        display: flex;
        flex-wrap: nowrap;
        align-content: center;
        justify-content: space-between;
    }
    .menu{
        display: flex;
        flex-direction: row;
        align-content: center;
        align-items: center;
        justify-content: space-between;
        background-color: var(--quinta-color);
        border-radius: 0px 0px 5px 5px;
        /* margin-right: -90%; */
        height: 30px;
        position: relative;
        box-sizing: border-box;
        overflow: visible;
        visibility: inherit;
        box-shadow: 4px 4px 7px rgba(98, 166, 175, 0.397);
        z-index: 2000;
    }
    nav a{
        font-family: Arial, Helvetica, sans-serif;
        text-decoration: none;
        color: var(--primeira-color);
        font-size: .8rem;
        text-align: center;
        line-height: 15px;
        padding: 4px;
        transition: .5s;
    }
    nav a:hover{
        background-color: var(--primeira-color);
        color: var(--quarta-color);
       
    }
    .serviços h3{
        display: none;
        font-family: var(--segunda-font);
        border: none;
        color: var(--quarta-color);
        font-size: .8rem;
        z-index: 1000;
        font-weight: bolder;
        background-color: var(--primeira-color);
        border-radius: 0px 0px 5px 5px;
        /* margin-left: -100%; */
        width: 90px;
        height: 30px;
        position: relative;
        box-sizing: border-box;
        overflow: visible;
        visibility: inherit;
        box-shadow: 4px 4px 7px rgba(98, 166, 175, 0.397);
        transition: .5s;
    }
    .serviços  h3:hover{
        font-size: 1rem;
        background-color: var(--quarta-color);
        font-family: var(--segunda-font);
        color: var(--primeira-color);
    }
    .serviços  a{
        font-size: .8rem;
        cursor: pointer;
       z-index: 1000;
    }
    .men button{
        font-family: var(--segunda-font);
        border: none;
        color: var(--quarta-color);
        z-index: 1000;
        font-weight: bolder;
        background-color: var(--primeira-color);
        border-radius: 0px 0px 5px 5px;
        margin-left: 2%;
        width: 70px;
        height: 15px;
        position: relative;
        box-sizing: border-box;
        overflow: visible;
        visibility: inherit;
        box-shadow: 4px 4px 7px rgba(98, 166, 175, 0.397);
        transition: .5s;
    }
    .men  a{
        font-size: .8rem;
        cursor: pointer;
       z-index: 1000;
    }
    .men a:hover{
        font-size: .9rem;
        color: var(--primeira-color);
        padding: .3px;
        border-radius: 2px;
    }
    .men ul a{
        display: block;
        background-color: rgba(255, 255, 255, 0.651);
        margin: 0px;
        border-radius: 3px
    }
    .men ul a:hover{
        font-size: 1rem;
        color: aliceblue;
        border-bottom: 2px solid aliceblue;
        display: block;
        background-color: rgba(0, 0, 0, 0.473);
        margin: 0px;
    }
    .men  p{
        font-size: .8rem;
        font-weight: bolder;
        background-color: var(--primeira-color);
        font-family: var(--segunda-font);
        color: var(--quarta-color);
        line-height: 15px;
        padding: 3px;
        border-radius: 0px 0px 5px 5px;
    }
    .men  p:hover{
        font-size: .9rem;
        background-color: var(--quarta-color);
        font-family: var(--segunda-font);
        color: var(--primeira-color);
        line-height: 15px;
        padding: 3px;
    }
    .destaque {
        font-size: 1.1rem;
        font-weight: bolder;
        background-color: var(--primeira-color);
        font-family: var(--segunda-font);
        color: var(--quarta-color);
        line-height: 25px;
        padding: 5px;
         transition: .5s;
    }
    
    .menu .destaque:hover{
        background-color: var(--quarta-color);
        font-family: var(--segunda-font);
        color: var(--primeira-color);
    }
    .serviços a:hover{
        font-size: 1rem;
        color: var(--primeira-color);
        padding: .5px;
        border-radius: 3px;
    }
}
