.body{
    background-image: url("../../midias/8.png");
    background-size: contain;
    width: 100%;
    background-repeat: no-repeat;
}
.texCentral{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 16%;
    margin-left: 1%;
    flex-wrap: wrap;
    align-content: flex-start;
}
.texCentral h1{
   font-family: var(--font-principal);
   color: var(--primeira-color);
   font-size: 3rem;
   margin: 0px;
   padding-bottom: 0px;
}
.ico{
    display: flex;
    gap: 10px;
    margin-top: 2%;
}
.texCentral span{
   font-family: var(--font-principal);
   color: var(--quarta-color);
   font-size: 4.5rem;
   background-color: var(--primeira-color);
   padding: .5rem;
   border-radius: 10px;
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
   cursor: pointer;
}
.texCentral span:hover{
   background-color: var(--quarta-color);
   font-size: 4.5rem;
   color: var(--primeira-color);
   padding: .8rem;
   border-radius: 10px;
   box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
}
.texCentral p{
   font-family: Arial, Helvetica, sans-serif;
   color: var(--setima-color);
   font-size: 2rem;
   margin: 0px;
   padding-top: 0px;
   text-align: initial;
}
.sobre{
    width: 70%;
    margin: 7% auto;
    margin-bottom: 3rem;
}
.sobre p{
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
    margin: 2rem;
}
.sobre h1{
    font-family: var(--font-principal);
    color: var(--primeira-color);
    font-size: 2.5rem;
    margin: 2px;
 }
 @media (min-width:280px){
    .body{
        background-image: none;
         position: relative;
     }
     .back{
         background-image: url('../../midias/9.png');
         background-repeat: no-repeat;
         background-size: 97%;
         margin: auto;
         margin-top: 15%;
     }
     .texCentral{
         padding: 1%;
         padding-top: 98%;
         align-content: center;
     }
     .texCentral h1{
        font-size: 2.1rem;
        padding: 4px;
     }
     .texCentral span{
        color: var(--primeira-color);
         font-size: 3rem;
         padding: .5rem;
         border-radius: 7px;
      }
      .texCentral span:hover{
        color: var(--quinta-color);
         font-size: 3rem;
         padding: .8rem;
      }
      a {
        color: var(--quinta-color);
         text-decoration: none;
     }
     a:hover {
        color: var(--primeira-color);
     }
      .texCentral p{
         font-size: 1.2rem;
      }
      .sobre{
          width: 93%;
          margin: 1% auto;
      }
      .sobre p{
          font-size: 1rem;
          margin: 0rem;
          margin-bottom: 1rem;
      }
      .sobre h1{
          font-size: 1.4rem;
          margin: 2px;
      }
      .sobre h2{
          font-size: 1rem;
          margin: 2px;
      }
 }
 @media (min-width:540px){
    .body{
        position: relative;
        background-image: url("../../midias/8.png");
        background-size: contain;
        width: 100%;
        margin-top: -18%;
        background-repeat: no-repeat;
     }
     .back{
        background-image: none;
     }
     .texCentral{
        padding: 1%;
        padding-top: 10%;
        margin-left: -51%;
    }
    .texCentral h1{
        font-size: 1.9rem;
     }
     .texCentral span{
         font-size: 2.3rem;
         padding: .5rem;
      }
      .texCentral span:hover{
         font-size: 2.3rem;
         padding: .8rem;
      }
      .sobre h1{
        font-size: 1.2rem;
        margin: 2px;
        padding: 2px;
    }

    .sobre h2{
        font-size: 1.2rem;
    }
    .sobre p{
     width: 85%;
     margin: auto;
    }
 }
