
@media (min-width:280px){
   .sobre{
      width: 90%;
      margin: auto;
  }
  .sobre p{
      font-size: 1rem;
  }
  .sobre h1{
      font-family: var(--segunda-font);
      color: var(--primeira-color);
      font-size: 1.3rem;
      margin-top: -7%;

   }
   .sobre h2{
      padding-top: 15%;
      font-size: 1.7rem;
   }
   .card{
      display: block;
      padding: 0px;
   }
   .card .tex{
      padding: 0%;
   }
   .card .tex p{
     font-family: Arial, Helvetica, sans-serif;
     font-size: 1rem;
  }
   .card .tex h2{
     font-size: 1.2rem;
  }
   .card div img{
     width: 100%;
     margin-top: 3rem;
   }
}
@media (min-width:360px){
   .sobre h2{
