
@media (min-width:280px){
    body{
        background-image: linear-gradient(to right,#1420501e, #cbd9ad48, #1828661e  );
    }
    .ma{
        padding-top: 15%;
        display: block;
    }
    .ma div img{
        width: 75%;
    }
    .ma h3{
        font-size: 1.7rem;
        color: var(--primeira-color);
    }
    .fundo{
        background-color: rgba(0, 0, 0, 0.356);
        margin: 0px;
        font-size: 1.3rem;
    }
    .rede{
        width: 99%;
        margin: auto;
    }
    .rede p{
        font-size: .9rem;
        font-family: Arial, Helvetica, sans-serif;
    }
    .ico{
        display: flex;
        justify-content: center;
        gap: 5px;
        background-color: rgba(0, 0, 0, 0.356);
        padding: .5rem;
    }
   
    .ico span{
       font-family: var(--font-principal);
       color: var(--quarta-color);
       font-size: 3.5rem;
       background-color: var(--primeira-color);
       padding: .3rem;
       border-radius: 10px;
       box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
    }
    
    .ico span:hover{
       background-color: var(--quarta-color);
       font-size: 3.5rem;
       color: var(--primeira-color);
       padding: .5rem;
       border-radius: 10px;
       box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.274);
    }
    .ico h2{
        font-size: 1rem;
        padding: 3px;
    }
    .tec{
        display: flex;
        align-items: center;
        gap: 1%;
        margin: 10%;
    }
    .tec p{
        padding: 5px 40px;
    
    } 
}
@media (min-width:540px){
    .ma h3{
        font-size: 2.4rem;
    }
    .fundo{
        font-size: 1.9rem;
    }

    .rede p{
        font-size: 1.1rem;
    }
    .ico{
        gap: 20px;
        padding: .8rem;
    }
    .ico span{
       font-size: 4.5rem;
       padding: .5rem;
    }
    .ico span:hover{
       font-size: 4.5rem;
       padding: .7rem;
    }
    .ico h2{
        font-size: 1.4rem;
        padding: 6px;
    }
    .tec{
        display: flex;
        align-items: center;
        gap: 5%;
        margin: 10%;
    }
}
@media (min-height: 280px) and (orientation: landscape) {
    .ma{
